import { Session } from './../../../services/session';
import { ENABLE_ELECTRONIC_SIGNATURE, OPEN_WIDGET_WAY } from './../../../services/constants';
import { Certificate } from './../../../models/certificate';
import { WidgetProvider } from './../../../providers/widget-provider';
import { TranslateService } from '@ngx-translate/core';
import { WorkflowProvider } from './../../../providers/workflow-provider';
import { NativeService } from './../../../services/native-service';
import { Component, ViewChild, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, Content, Events, AlertController } from 'ionic-angular';
import { Webpage, ApproveTask } from '../../../models/models';
/**
 *  
 *  @param from  workflowdetails 來源 'message','undonetask','donetask','mytask'
 */

@IonicPage()
@Component({
  selector: 'page-workflow-details',
  templateUrl: 'workflow-details.html',
})
export class WorkflowDetailsPage {

  taskItem: any;
  approval: string = ''
  Head: any;
  ApprovalInfo: any;
  formItems: any[];
  taskDetails: any;
  from: string = '';
  isFinished: boolean = false;
  isLoadingData: boolean = false;
  sourceUrl: boolean = false;
  flag: string = 'C'
  isNeedSign = false;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public nativeService: NativeService,
    public wfprovider: WorkflowProvider,
    public translateService: TranslateService,
    public alertCtrl: AlertController,
    public events: Events,
    private widgetProvider: WidgetProvider,
    private cdf: ChangeDetectorRef,
    private session: Session) {
    this.taskItem = this.navParams.data;
    this.isFinished = this.navParams.data.isFinished;
    this.from = this.navParams.data.from;
    this.getDetails()
  }

  @ViewChild('nameInput') nameInput;
  @ViewChild(Content) content: Content;

  /**
   * Flag標識，
   * N流程沒有做移動簽核，
   * B流程有做移動簽核但該節點不行，
   * C表示該節點不是Ready狀態(是InActive或Complete等)或者已做簽核標記，
   * Y表示可以手機簽核 (只有在Y狀態下才可以審核)
   */
  getDetails(_RefreshEvent?) {
    this.isLoadingData = true;
    this.sourceUrl = false
    this.wfprovider.getTaskDetails(this.taskItem.pid, this.taskItem.taskid, this.taskItem.modelname).subscribe(res => {
      if (res.result) {
        /*  && res.result.SourceUrl[0].sourceurl */
        if (res.result.SourceUrl && res.result.SourceUrl.length > 0) {
          this.sourceUrl = res.result.SourceUrl[0].detailflag == 'Y'
          // this.sourceUrl = 'https://www.popochiu.com/file/download/getpdf' || res.result.SourceUrl[0].sourceurl ;
          this.isNeedSign = res.result.SourceUrl[0].signflag === 'Y' && ENABLE_ELECTRONIC_SIGNATURE;
        }
        // else {
        //   this.sourceUrl = 'https://www.popochiu.com/file/download/getpdf' || res.result.SourceUrl[0].sourceurl ;
        // }
        this.taskDetails = (res.result.Head && res.result.Head.length > 0) ? res.result.Head[0] : { flag: 'C' }
 
        this.ApprovalInfo = res.result.ApprovalInfo;
        if (!this.isFinished) {
          this.flag = this.taskDetails.flag && this.taskDetails.flag.toUpperCase()
        }
        this.Head = this.createFormMain(res.result.Head);
        this.formItems = this.createForm(res.result.Items);
      }
      setTimeout(() => {
        this.cdf && this.cdf.detectChanges()
        this.content && this.content.resize()
      }, 100)
    }, err => {

    },
      () => {
        this.isLoadingData = false;
        _RefreshEvent && _RefreshEvent.complete()
      })
  }

  doRefresh(event) {
    this.getDetails(event)
  }
  /**
   * 创建表单主体信息
   * @param metaarray 
   */
  createFormMain(metaarray) {
    if (!metaarray || metaarray.length == 0) return [];
    let resarray = [];

    for (let Items of metaarray) {
      let i = 0;
      let obj = Items;
      let array = Object.keys(obj);
      for (let item of array) {
        if (i > 5) {
          let value = {
            keyname: item,
            value: obj[item]
          };
          resarray.push(value);
        }
        i++;
      }
    }
    return resarray;
  }

  /**
   * 创建表单数组
   * @param metaarray 
   */
  createForm(metaarray) {
    if (!metaarray || metaarray.length == 0) return [];
    let final = [];
    for (let Items of metaarray) {
      let resarray = [];
      let obj = Items;
      let array = Object.keys(obj);
      for (let item of array) {
        let value = {
          keyname: item,
          value: obj[item]
        }
        resarray.push(value)
      }
      final.push(resarray)
    }
    return final
  }

  slideWidth(index) {
    if (this.formItems.length == index) {
      return {
        "width": screen.availWidth + 'px'
      }
    } else {
      return {
        "width": screen.availWidth - 100 + 'px !important'
      }
    }
  }
  ionViewDidLoad() {
  }



  rejectTask() {
    if (!this.approval) {
      this.nativeService.showToast(this.translateService.instant('WORKFLOW_DETAILS_REJECT_TOAST'))
      this.content.scrollToBottom()
      return
    }

    this.doApproveTask('Reject')
  }

  adoptTask() {

    if (this.isNeedSign) { // 需要电子签章

      this.wfprovider.getCertification().then(cer => {
        this.doApproveTask('Approve', cer)
      }).catch((err: { errcode: number, msg: string }) => { // 取不到电子签章
        switch (err.errcode) {
          case -1:
            this.notConfigCert();
            break;
          case -2:
            this.certIsUnexpected();
            break;
          case -3:
            this.certIsExpired();
            break;
          case -4:
            this.certIsUnexpected();
            break;
          case -5:
            this.nativeService.showToast(this.translateService.instant('EMPLOYEE_NOT_BIND_YET'))
            break;
          case -6:
            this.certNotMatchCurrentUser();
            break;
          default:
            this.certIsUnexpected();
        }
      }
      );

    } else {
      this.doApproveTask('Approve')
    }

  }

  // 证书所有者与当前用户不匹配
  certNotMatchCurrentUser() {
    const title = this.translateService.instant('CERTIFICATE_NOMATCH_TITLE')
    const subTitle = this.translateService.instant('CERTIFICATE_NOMATCH_SUBTITLE')
    const message = `${this.translateService.instant('CERTIFICATE_NOMATCH_SUBJECT')}${this.wfprovider.getCertSubjectName(this.wfprovider.cert)}
    <br/>
    ${this.translateService.instant('CERTIFICATE_NOMATCH_CURRENT')}${this.session.user.employeeInfo.employeeName}
    `
    this.nativeService.alert(title, subTitle, message)
  }

  certIsUnexpected() {
    this.nativeService.showToast(this.translateService.instant('WORKFLOW_DETAILS_CERT_EXCEPTION'))
  }
  // 证书已过期
  certIsExpired() {
    this.nativeService.showToast(this.translateService.instant('WORKFLOW_DETAILS_CERT_EXPIRED'))
    this.notConfigCert();
  }
  notConfigCert() {
    const alert = this.alertCtrl.create()
    alert.setTitle(this.translateService.instant('WORKFLOW_DETAILS_CERT_TOAST'))
    alert.setSubTitle(this.translateService.instant('WORKFLOW_DETAILS_CERT_HINT'))
    alert.addButton({
      text: this.translateService.instant('WORKFLOW_DETAILS_CANCEL'),
      handler: data => {

      }
    });
    alert.addButton({
      text: this.translateService.instant('WORKFLOW_DETAILS_CERT_SET'),
      handler: data => {
        this.navCtrl.push('CertificatePage', {
          FromWF: true
        })
      }
    });
    alert.present();
  }
  /**
   * Status為Approve時為同意， Reject為拒單
   * @param type 

   */
  doApproveTask(type, cert?: Certificate) {


    const approveTask: ApproveTask = {
      ModelName: this.taskItem.modelname,
      PID: this.taskItem.pid,
      TID: this.taskItem.taskid,
      Status: type,
      UserCode: '',
    }

    if (this.isNeedSign && cert) {
      approveTask.SignFlag = 'Y';
      approveTask.Cert = cert.encryptData;
      approveTask.Pwd = cert.password;
    }

    this.wfprovider.doApproveTask(approveTask).subscribe(() => {
      setTimeout(() => {
        this.events.publish('workflow:refresh-todo-list');
      }, 1000)
      this.afterApproveTask()
    }, err => {
      const errorCode = err.errcode
      this.nativeService.showToast(errorCode + ':' + this.translateService.instant('WORKFLOW_DETAILS_SIGN_FAILED'))
    });
  }
  afterApproveTask() {
    if (this.from == 'message') {
      let alert = this.alertCtrl.create()
      alert.setTitle(this.translateService.instant('WORKFLOW_DETAILS_APPROVE_FINISH_TITLE'))
      alert.setSubTitle(this.translateService.instant('WORKFLOW_DETAILS_APPROVE_FINISH_SUBTITLE'))
      alert.addButton({
        text: this.translateService.instant('WORKFLOW_DETAILS_APPROVE_CLOSE'),
        handler: data => {
          this.navCtrl.pop();
        }
      });
      alert.addButton({
        text: this.translateService.instant('WORKFLOW_DETAILS_APPROVE_GOTODO'),
        handler: data => {
          this.navCtrl.pop();
          this.navCtrl.push('WorkflowPage')
        }
      });
      alert.present();
    } else {
      this.navCtrl.pop();
    }
  }

  openSourceForm(sourceUrl) {
    let page: Webpage = {
      url: sourceUrl,  // 链接地址
      title: this.taskDetails.processnametw,  // 标题
      description: this.taskDetails.taskdesctw,   // 摘要内容
      imageUrl: "",   // 图片路径
      appId: "0", // 應用ID
      options: {}
    };
    this.navCtrl.push('PdfjsViewerPage', { title: this.taskDetails.processnametw, pid: this.taskDetails.pid})
    // this.widgetProvider.openByBrowser(OPEN_WIDGET_WAY.INAPPBROWSER, sourceUrl, this.navCtrl, null);
  }


}



